<template>
  <div id="swings">
    <div class="row">
      <div class="col-6 col-sm-4 col-md-3 mx-auto" v-for="i of Object.keys(swings)" :key="i">
        <div data-toggle="tooltip" data-html="true" data-placement="bottom" :title="formatElecs(i, flipByState[i])">
            <h5>{{ i }}</h5>
            <p>{{ mapName(i, swings[i].incumbent) }} {{ swings[i][swings[i].incumbent] > 0 ? '+' : '' }}{{ swings[i][swings[i].incumbent].toFixed(1) }}%</p>
            <div :id="'swing-' + i" class="swing"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
@media (min-width: 1200px) {
  #swings {
    padding-top: 15px;
  }
}

.row {
  margin-top: 20px;
  padding: 0 20px;
}

.row > div {
  padding: 15px 0;
}

.row > div h5, .row > div p {
  text-align: center;
  margin: 0;
  margin-bottom: 5px;
}

.swing {
  height: 50px;
  width: 100px;
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  margin-top: 10px;
}

.swing::before {
  content: "";
  position: absolute;
  height: 100px;
  width: 100px;
  z-index: -1;
  background: url('../assets/untitled.svg') 0 0 repeat;
}

#swing-nsw, #swing-qld, #swing-wa {
  transform: scaleX(-1);
}

#swing-NSW::before { transform: rotate(calc( var(--NSW) /10*90deg)); }
#swing-VIC::before { transform: rotate(calc( var(--VIC) /10*90deg)); }
#swing-QLD::before { transform: rotate(calc( var(--QLD) /10*90deg)); }
#swing-WA::before { transform: rotate(calc( var(--WA) /10*90deg)); }
#swing-SA::before { transform: rotate(calc( var(--SA) /10*90deg)); }
#swing-TAS::before { transform: rotate(calc( var(--TAS) /10*90deg)); }
#swing-ACT::before { transform: rotate(calc( var(--ACT) /10*90deg)); }
#swing-NT::before { transform: rotate(calc( var(--NT) /10*90deg)); }
</style>

<script>
import $ from 'jquery'
import * as p from '../../pendulum';
import * as s from '../../swings';

export default {
  name: 'Swings',

  data() {
    return {
      swings: {},
      flipByState: {}
    }
  },

  methods: {
    mapName(state, incumbent) {
      if (incumbent == 'LNC') {
        if (state == 'QLD') return 'LNP'
        if (state == 'NT') return 'CLP'
        return 'LIB'
      } else {
        return 'ALP'
      }
    },

    formatElecs(state, elecs) {
      let makeList = (x) => {
        x.reverse()
        if (x.length == 0) return ''
        if (x.length == 1) return x[0]
        if (x.length == 2) return x[0] + ' & ' + x[1]
        return x.slice(0, -1).join(', ') + ' & ' + x[x.length - 1] //fix
      }

      let flipped = {
        'ALP': [],
        'CLP': [],
        'NP': [],
        'LNP': [],
        'LP': [],
        'IND': [],
        'GRN': [],
        'CA': [],
        'KAP': []
      }

      for (let i of elecs) {
        flipped[i.party].push(i)
      }

      let final = []
      for (let i in flipped) {
        for (let j in flipped) {
          let newFlipped = flipped[i].filter(x => x.opposition == j)
          if (newFlipped.length > 0) {
            final.push(`<b>${newFlipped.length} ${j == 'LP' ? 'LIB' : (j == 'NP' ? 'NAT' : j)} &rarr; ${i == 'LP' ? 'LIB' : (i == 'NP' ? 'NAT' : i)}</b>`)
            final.push(makeList(newFlipped.map(x => x.division)))
          }
        }
      }
      return final.length == 0 ? '<b>No Change</b>' : final.join('<br>')
    }
  },
  
  created() {
    let flipped = []
    for (let i of p['ALP']) if (i.flip) flipped.push(i)
    for (let i of p['LNC']) if (i.flip) flipped.push(i)

    let flipByState = {
      'NSW': [],
      'VIC': [],
      'QLD': [],
      'WA': [],
      'SA': [],
      'TAS': [],
      'ACT': [],
      'NT': []
    }

    for (let i of flipped) flipByState[i.state].push(i)
    this.flipByState = flipByState

    let swings = s.default
    let sortedSwings = Object.keys(swings).sort((a, b) => {
      let cmp = this.flipByState[a].length - this.flipByState[b].length
      if (cmp != 0) return cmp
      return Math.abs(swings[a].LNC) - Math.abs(swings[b].LNC)
    })
    sortedSwings.reverse()

    for (let i of sortedSwings) {
      this.swings[i] = swings[i]
    }
  },

  mounted() {
    $(function () {
      $('[data-toggle="tooltip"]').tooltip()
    })

    for (let i in this.swings) {
      let element = this.$el.querySelector('#swing-' + i)
      if (this.swings[i].incumbent == 'LNC') element.style.transform = 'scaleX(-1)'
      element.style.setProperty('--' + i, this.swings[i].ALP > 10 ? 10 : this.swings[i].ALP)
    }
  }
}
</script>

