<template>
  <div id="app">
    <div class="row">
      <div class="col-lg-12 col-xl-6 ml-0 pt-4 pb-2">
        <!-- HEADER -->
        <Header />

        <!-- RESULT -->
        <hr class="mt-4">
        <div id="result" class="text-center">
          <p>If an election was held today,{{ winner == 'COALITION' ? ' the' : '' }}</p>
          <h2 id="winner"><span :style="{ backgroundColor: winnerColor }">{{ winner }}</span></h2>
          <p id="winner-res">
            would {{ winner == 'NO PARTY' ? 'clearly' : 'likely' }} form government.
            <!-- <Help text="OzPredict asks what would happen if an a election was held right now (<i>not</i> what would happen on election day)" /> -->
          </p>
          <hr class="big-swings">
          <b id="prog-title" class="mt-4" style="font-size: 0.85rem">Probability of each outcome <Help text="This is the probability that a party will secure majority or minority government" /></b>
          <div id="progress-real" class="prog progress mx-auto my-2" style="font-weight: bold">
            <div class="progress-bar" role="progressbar" :style="{ width: (probs.majALP * 100 + (0.01 > probs.majALP ? 1 : 0)).toFixed(0) + '%', backgroundColor: toRGB(colors.ALP) }">{{ probs.majALP > 0.05 ? (probs.majALP * 100).toFixed(0) + '%' : '' }}</div>
            <div class="progress-bar" role="progressbar" :style="{ width: (probs.minALP * 100 + (0.01 > probs.minALP ? 1 : 0)).toFixed(0) + '%', backgroundColor: toRGB(colors.ALP, 0.55) }">{{ probs.minALP > 0.05 ? (probs.minALP * 100).toFixed(0) + '%' : '' }}</div>
            <div class="progress-bar" role="progressbar" :style="{ width: (probs.hung * 100 + (0.01 > probs.hung ? 1 : 0)).toFixed(0) + '%', backgroundColor: toRGB(colors.IND) }">{{ probs.hung > 0.05 ? (probs.hung * 100).toFixed(0) + '%' : '' }}</div>
            <div class="progress-bar" role="progressbar" :style="{ width: (probs.minLNC * 100 + (0.01 > probs.minLNC ? 1 : 0)).toFixed(0) + '%', backgroundColor: toRGB(colors.LNP, 0.6) }">{{ probs.minLNC > 0.05 ? (probs.minLNC * 100).toFixed(0) + '%' : '' }}</div>
            <div class="progress-bar" role="progressbar" :style="{ width: (probs.majLNC * 100 + (0.01 > probs.majLNC ? 1 : 0)).toFixed(0) + '%', backgroundColor: toRGB(colors.LNP) }">{{ probs.majLNC > 0.05 ? (probs.majLNC * 100).toFixed(0) + '%' : '' }}</div>
          </div>
          <div id="progress-desc" class="prog row mx-auto" style="font-size: 0.75rem"> <!-- change 0.00 to 0.06 for small percentages -->
            <div class="col-4 col-sm-2 mx-auto pd"><span :style="{ color: toRGB(colors.ALP) }">■</span> Labor majority {{ 0.00 > probs.majALP ? ` (${0.01 > probs.majALP ? '&lt;1' : (probs.majALP * 100).toFixed(0)}%)` : '' }}</div>
            <div class="col-4 col-sm-2 mx-auto pd"><span :style="{ color: toRGB(colors.ALP, 0.55) }">■</span> Labor minority {{ 0.00 > probs.minALP ? ` (${0.01 > probs.minALP ? '&lt;1' : (probs.minALP * 100).toFixed(0)}%)` : '' }}</div>
            <div class="col-4 col-sm-2 mx-auto pd"><span :style="{ color: toRGB(colors.IND) }">■</span> Tossup {{ 0.00 > probs.hung ? ` (${0.01 > probs.hung ? '&lt;1' : (probs.hung * 100).toFixed(0)}%)` : '' }}</div>
            <div class="col-2"></div>
            <div class="col-4 col-sm-2 mx-auto pd"><span :style="{ color: toRGB(colors.LNP, 0.6) }">■</span> Lib-Nat minority {{ 0.00 > probs.minLNC ? ` (${0.01 > probs.minLNC ? '&lt;1' : (probs.minLNC * 100).toFixed(0)}%)` : '' }}</div>
            <div class="col-4 col-sm-2 mx-auto pd"><span :style="{ color: toRGB(colors.LNP) }">■</span> Lib-Nat majority {{ 0.00 > probs.majLNC ? ` (${0.01 > probs.majLNC ? '&lt;1' : (probs.majLNC * 100).toFixed(0)}%)` : '' }}</div>
            <div class="col-2"></div>
          </div>
        </div>

        <!-- SWINGS (big) -->
        <Swings class="big-swings" />
        <p class="w-100 text-center text-muted mt-3 mb-0">updated {{ time }}</p>
      </div>

      <div class="col-md">
        <hr class="small-swings pb-3">
        <div id="selector" class="text-center">
          <ul class="pagination pagination-sm d-flex align-items-center" style="font-size: 1.2rem">
            &rarr;
            <li class="page-item ml-3" :class="{ active: activeTab == 'chamber' }">
              <a class="page-link" data-target="#toggle-chamber" v-on:click="toggleChamber">Chamber</a>
            </li>
            <li class="page-item small-swings" :class="{ active: activeTab == 'swings' }">
              <a class="page-link" data-target="#toggle-swings" v-on:click="toggleSwings">Swings</a>
            </li>
            <li class="page-item mr-3" :class="{ active: activeTab == 'pendulum' }">
              <a class="page-link" data-target="#toggle-pendulum" v-on:click="togglePendulum">Pendulum</a>
            </li>
            &larr;
          </ul>
          <small class="text-muted mx-auto">(Most likely outcome shown)</small>
        </div>
        

        <!-- CHAMBER -->
        <Chamber id="toggle-chamber" :colors="colors" />
        <!-- SWINGS (small) -->
        <Swings id="toggle-swings" class="small-swings" />
        <!-- PENDULUM -->
        <Pendulum id="toggle-pendulum" class="overflow-auto mx-auto" :colors="colors" />
      </div>
    </div>
  </div>
</template>

<style scoped>
