</style>

<script>
import moment from 'moment-timezone'
import $ from 'jquery'
import * as c from '../probabilities'
import Header from "@/components/Header.vue"
import Swings from "@/components/Swings.vue"
import Pendulum from "@/components/Pendulum.vue"
import Chamber from "@/components/Chamber.vue"
import Help from "@/components/Help.vue"

export default {
  name: 'App',
  components: {
    Header,
    Swings,
    Pendulum,
    Chamber,
    Help
  },

  methods: {
    toggleChamber() {
      $('#toggle-chamber').slideDown()
      $('#toggle-swings').slideUp()
      $('#toggle-pendulum').slideUp()
      this.activeTab = 'chamber'
    },
    toggleSwings() {
      $('#toggle-chamber').slideUp()
      $('#toggle-swings').slideDown()
      $('#toggle-pendulum').slideUp()
      this.activeTab = 'swings'
    },
    togglePendulum() {
      $('#toggle-chamber').slideUp()
      $('#toggle-swings').slideUp()
      $('#toggle-pendulum').slideDown()
      this.activeTab = 'pendulum'
    },
    watchSwings(x) {
      if (x.matches && this.activeTab == 'swings') {
        this.toggleChamber()
      }
    },
    toRGB(hex, alpha = 1) {
      const [r, g, b] = hex.match(/\w\w/g).map(x => parseInt(x, 16));
      return `rgba(${r},${g},${b},${alpha})`;
    }
  },

  data() {
    return {
      colors: {
        'ALP': '#F00011',
        'LP': '#080CAB',
        'NP': '#008000',
        'LNP': '#1456F1',
        'CLP': '#FF7F00',
        'GRN': '#10C25B',
        'KAP': '#B50204',
        'CA': '#FF6300',
        'IND': '#888888'
      },
      time: '2021-04-19 14:20',
      activeTab: 'chamber',
      winner: '',
      probs: {}
    }
  },
  
  created() {
    this.time = moment.tz(this.time, 'Australia/Sydney').tz(moment.tz.guess()).format('h:mma D MMM YYYY');
    let probs = c.default
    this.probs = probs

    if (probs.majALP + probs.minALP >= 0.5) {
      this.winner = 'LABOR'
      this.winnerColor = this.colors['ALP']
    } else if (probs.majLNC + probs.minLNC >= 0.5) {
      this.winner = 'COALITION'
      this.winnerColor = this.colors['LP']
    } else {
      this.winner = 'NO PARTY'
      this.winnerColor = this.colors['IND']
    }
  },

  mounted() {
    $('#toggle-swings').hide()
    $('#toggle-pendulum').hide()

    $(function () {
      $('[data-toggle="tooltip"]').tooltip()
    })

    if (this.probs.majLNC + this.probs.minLNC > this.probs.majALP + this.probs.minALP) {
      $.fn.reverseChildren = function () {
        return this.each(function (){
          var $this = $(this)
          $this.children().each(function () { $this.prepend(this) })
        })
      }
      $('#progress-real').reverseChildren()
      $('#progress-desc').reverseChildren()
    }

    let x = window.matchMedia("(min-width: 1200px)")
    this.watchSwings(x)
    x.addListener(this.watchSwings)  
  }
}
</script>
