<template>
  <div>
    <div class="row">
      <div id="chamber-container" class="col-xs-12 col-sm-8">
        <canvas id="chamber"></canvas>
      </div>

      <div id="party-list" class="col-sm-12 col-md-4 py-5">
        <table class="table table-borderless">
          <tr class="party-category">
            <td>Government<span v-if="Object.values(gov.government).reduce((a, b) => a + b) < 76">?</span></td>
            <td class="text-right">{{ Object.values(gov.government).reduce((a, b) => a + b) }}</td>
          </tr>
          <tr class="text-muted" v-for="i of Object.keys(gov.government)" :key="i">
            <td><span class="party" :party="i">⬤</span> <span v-html="parties[i]"></span></td>
            <td class="text-right">{{ gov.government[i] }}</td>
          </tr>
          <tr class="text-muted" v-if="Object.values(gov.government).reduce((a, b) => a + b) < 76">
            <td>Crossbench</td>
            <td class="text-right">
              {{ 76 - Object.values(gov.government).reduce((a, b) => a + b) }} <Help text="Because of a hung parliament, the government would need the support of this many crossbenchers" />
            </td>
          </tr>
          <tr class="party-category">
            <td>Opposition</td>
            <td class="text-right">{{ Object.values(gov.opposition).reduce((a, b) => a + b) }}</td>
          </tr>
          <tr class="text-muted" v-for="i of Object.keys(gov.opposition)" :key="i">
            <td><span class="party" :party="i">⬤</span> <span v-html="parties[i]"></span></td>
            <td class="text-right">{{ gov.opposition[i] }}</td>
          </tr>
          <tr class="party-category">
            <td>Crossbench</td>
            <td class="text-right">{{ Object.values(gov.crossbench).reduce((a, b) => a + b) }}</td>
          </tr>
          <tr class="text-muted" v-for="i of Object.keys(gov.crossbench)" :key="i">
            <td><span class="party" :party="i">⬤</span> <span v-html="parties[i]"></span></td>
            <td class="text-right">{{ gov.crossbench[i] }}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
  
</template>

<style scoped>
.row div {
  display: flex;
  justify-content: center;
  align-items: center;
}

#chamber {
  display: block;
  object-fit: contain;
}

.table td {
  padding: 5px;
}

.party {
  font-weight: bold;
  color: #FFFFFF;
}

.party-category {
  font-weight: bold;
}

@media (min-width: 768px) {
  #party-list {
    padding-right: 0 !important;
  }
}

@media (max-width: 769px) {
  #chamber-container, table {
    width: 70%
  }
}
</style>

<script>
import $ from 'jquery'
import * as p from '../../pendulum';
import Help from '@/components/Help.vue'

export default {
  name: 'Chamber',
  props: {
    colors: Object
  },
  components: {
    Help
  },

  methods: {
    initCanvas() {
      const canvas = document.getElementById('chamber')
      canvas.width = (this.startX*2 + this.gap*(this.block.width*2+this.middle-2)) * window.devicePixelRatio
      canvas.height = (this.startY*2 + this.gap*(this.block.height+this.cross.length*1.3)) * window.devicePixelRatio
      canvas.style.width = (this.startX*2 + this.gap*(this.block.width*2+this.middle-2)) + 'px'
      canvas.style.height = (this.startY*2 + this.gap*(this.block.height+this.cross.length*1.3)) + 'px'
      canvas.getContext('2d').scale(window.devicePixelRatio, window.devicePixelRatio)
    },

    drawBlock(x, y, flip) {
      const canvas = document.getElementById('chamber')
      if (canvas.getContext) {
        let ctx = canvas.getContext('2d')
        for (let i = 0; i < this.block.height; i++) {
          for (let j = 0; j < this.block.width; j++) {
            ctx.beginPath()
            ctx.fillStyle = this.pickColor()
            let newX = x + j*this.gap
            let newY = flip ? (y + (this.block.height - 1)*this.gap - i*this.gap) : (y + i*this.gap)
            ctx.arc(newX, newY, this.radius, 0, 2*Math.PI, 0)
            ctx.fill()
          }
        }
      }
    },

    drawCross(x, y) {
      const canvas = document.getElementById('chamber')
      if (canvas.getContext) {
        let ctx = canvas.getContext('2d')
        for (let i = 0; i < this.cross.length; i++) {
          for (let j = 0; j < this.cross[i]; j++) {
            let relX = Math.cos(Math.PI * j/(this.cross[i]-1)) * (this.gap*this.middle/2 + i*this.gap) //
            let relY = Math.sin(Math.PI * j/(this.cross[i]-1)) * (this.gap*(this.middle+1)/3 + i*this.gap) //
            ctx.beginPath()
            ctx.fillStyle = this.pickColor()
            ctx.arc(x - relX, y + relY, this.radius, 0, 2*Math.PI, 0)
            ctx.fill()
          }
        }
      }
    },

    drawSpeaker(x, y) {
      const canvas = document.getElementById('chamber')
      if (canvas.getContext) {
        let ctx = canvas.getContext('2d')
        ctx.beginPath()
        ctx.fillStyle = this.pickColor()
        ctx.arc(x, y, 1.3*this.radius, 0, 2*Math.PI, 0)
        ctx.fill()
      }
    },

    initColors() {
      let newColors = []
      for (let i = 0; i < this.block.width*this.block.height+1; i++) {
        newColors.push(this.results[i])
      }

      let crossElems = []
      for (let i = 0; i < this.cross.length; i++) {
        for (let j = 0; j < this.cross[i]; j++) {
          let rotation = Math.PI * j/(this.cross[i]-1)
          let distance = i
          crossElems.push({ rotation, distance })
        }
      }
      crossElems = crossElems.sort((a, b) => {
        return a.rotation - b.rotation == 0 ? b.distance - a.distance : a.rotation - b.rotation
      })
      crossElems = crossElems.map((e, i) => {
        e.res = this.results[this.block.width*this.block.height+1 + i]
        return e
      })      
      crossElems = crossElems.sort((a, b) => {
        return a.distance - b.distance == 0 ? a.rotation - b.rotation : a.distance - b.distance
      })
      newColors = newColors.concat(crossElems.map(x => x.res))

      for (let i = this.block.width*this.block.height+this.cross.reduce((t, n) => t + n)+1; i < 151; i++) {
        newColors.push(this.results[i])
      }

      this.newColors = newColors
    },

    pickColor() {
      let res = this.colors[this.newColors[this.colorCounter]]
      this.colorCounter++
      return res
    },

    partyColors() {
      let parties = document.getElementsByClassName('party')
      for (let i of parties) i.style.color = this.colors[i.attributes.party.nodeValue]
    }
  },

  data() {
    return {
      startX: 20,
      startY: 20,
      middle: 3,
      radius: 10,
      gap: 30,
      block: {
        width: 5,
        height: 9
      },
      cross: [5, 9, 12, 15, 19],
      niceResults: [],
      results: [],
      gov: {
        government: {},
        opposition: {},
        crossbench: {}
      },
      parties: {
        'LP': 'Liberal',
        'NP': 'National',
        'LNP': 'LNP <small>QLD</small>',
        'CLP': 'CLP <small>NT</small>',
        'ALP': 'Labor',
        'GRN': 'Greens',
        'KAP': 'Katter\'s',
        'CA': 'Centre All.',
        'IND': 'Independent',
        'ON': 'One Nation'
      },
      colorCounter: 0,
      newColors: []
    }
  },

  created() {
    let results = {
      // CHANGE THESE
      'ALP': [],
      'IND': [],
      'GRN': [],
      'KAP': [],
      'CA': [],
      'CLP': [],
      'NP': [],
      'LNP': [],
      'LP': []
    }

    for (let i of p['ALP']) results[i.party].push(i)
    for (let i of p['LNC']) results[i.party].push(i)

    this.niceResults = results
    for (let i in results) {
      this.results = this.results.concat(...Array(results[i].length).fill(i))
    }

    if (results.ALP.length > results.LP.length + results.LNP.length + results.NP.length + results.CLP.length) {
      this.gov.government['ALP'] = results.ALP.length
      this.gov.opposition['LP'] = results.LP.length
      this.gov.opposition['LNP'] = results.LNP.length
      this.gov.opposition['NP'] = results.NP.length
      if (results.CLP.length > 0) this.gov.opposition['CLP'] = results.CLP.length
    } else {
      this.gov.government['LP'] = results.LP.length
      this.gov.government['LNP'] = results.LNP.length
      this.gov.government['NP'] = results.NP.length
      if (results.CLP.length > 0) this.gov.government['CLP'] = results.CLP.length
      this.gov.opposition['ALP'] = results.ALP.length
      this.results.reverse()
    }
    this.gov.crossbench.IND = 0
    for (let i in results) {
      if (!['ALP', 'LP', 'LNP', 'NP', 'CLP'].includes(i)) {
        this.gov.crossbench[i] = results[i].length
      }
    }
  },

  mounted() {
    $(function () {
      $('[data-toggle="tooltip"]').tooltip()
    })

    let width = this.$el.querySelector('#chamber-container').offsetWidth
    this.radius = window.innerWidth >= 768 ? width / 35 : width / 35
    this.gap = this.radius * 3

    this.initColors()
    this.initCanvas()
    this.drawSpeaker(this.startX + this.gap*(this.block.width-1+this.middle/2), this.startY)
    this.drawBlock(this.startX, this.startY + this.gap, false)
    this.drawCross(this.startX + this.gap*(this.block.width-1+this.middle/2), this.startY + this.gap*this.block.height + this.gap) //
    this.drawBlock(this.startX + this.gap*(this.block.width-1+this.middle), this.startY + this.gap, true)
    this.partyColors()
  }
}
</script>

